*,
::before,
::after{
  margin: 0;
  padding: 0;

  -webkit-tap-highlight-color: transparent;/*清除点击默认的高亮效果*/

  -webkit-box-sizing: border-box;/*以你的border开始计算你的宽度*/
}
html {
  overflow-y: scroll;
}
body{
  font-size: 14px;
  font-family: "Microsoft YaHei",sans-serif;/*第二个是手机的一个默认字体*/
  background-color: #f0f0f0;
  /*padding-left: calc(100vw - 100%);*/
}
a{
  text-decoration: none;
  color: #000;
}
ul{
  list-style: none;
}
input,textarea, button{
  border: none;
  resize: none;
  outline: none;/*清除选中效果*/

  -webkit-appearance: none;/*清除webkit内核浏览器 默认样式*/
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"]{
  -moz-appearance: textfield;
}

/*清除浮动*/
.clearfix::before,
.clearfix::after{
  content: ".";
  display: block;
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
#app {
  padding-top: 59px;
}

/* 公用1：文本超出省略号显示 */
.overflow-text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.hide {
  display: none;
}

/* 公用2： */

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
.el-table__body-wrapper::-webkit-scrollbar
{
  width: 3px;
  height: 10px;
  background-color: #FFF;
}

/*定义滚动条轨道 内阴影+圆角*/
.el-table__body-wrapper::-webkit-scrollbar-track
{
  /*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);*/
  border-radius: 10px;
  background-color: #FFF;
}

/*定义滑块 内阴影+圆角*/
.el-table__body-wrapper::-webkit-scrollbar-thumb
{
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: rgba(0, 0, 0, .48);
}

/* 公用3：分页按钮样式 */
.public-pagination /deep/ .el-pagination .el-pager .active {
  background-color: #f48118 !important;
}
.public-pagination /deep/ .el-pagination .el-pager>li:hover{
  color: #f48118 !important;
}
.public-pagination /deep/ .el-pagination .el-pager>.active:hover{
  color: #fff !important;
}

/* 公用4：动画 */

/* 动画：下20到上 */
.fade-enter-to,.fade-leave-to {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fade-enter,.fade-leave-to{
  opacity: 0;
  -webkit-transform: translate3d(0,20px,0);
          transform: translate3d(0,20px,0);
}

/* 动画：上20到下 */
.bTtFade-enter-to,.bTtFade-leave-to {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.bTtFade-enter,.bTtFade-leave-to{
  opacity: 0;
  -webkit-transform: translate3d(0, -20px, 0);
          transform: translate3d(0, -20px, 0);
}

